.card{
    width: 300px;
    margin: 30px auto;
    padding: 7px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    border: 1px solid #ccc;
    box-shadow: 1px 2px 2px 1px #222;
    border-radius: 20px;
}

.card h2{
    color: #fff;
}

img{
    width: 6em;
}